import React, { FormEvent, useState } from 'react';
import { Stepper, Step } from 'react-form-stepper';
import { ZodType, isValid, z } from 'zod';

import './styles.css';
import { useMultyStepForm } from '../../hooks/useMultiStepForm';
import UserForm from './UserForm';
import UserFormOrder from './UserFormOrder';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import CustomLabelFields from '../UI/custom-fields/CustomLabelFields';
import CustomInputFields from '../UI/custom-fields/CustomInputFields';

type FormData = {
  firstName: string;
  lastName: string;
  phone: string;
};

const initialState: FormData = {
  firstName: '',
  lastName: '',
  phone: '',
};

const phoneRegex = new RegExp(
  /^([+]?[\s0-9]+)?(\d{3}|[(]?[0-9]+[)])?([-]?[\s]?[0-9])+$/,
);

export const schema: ZodType<FormData> = z.object({
  firstName: z.string().trim().min(2).max(50),
  lastName: z.string().trim().min(2).max(50),
  phone: z.string().regex(phoneRegex, 'Invalid Number!'),
});

export default function FormStep({ item }: any) {
  const [data, setData] = useState(initialState);
  function updateFields(fields: Partial<FormData>) {
    setData((prev) => {
      return { ...prev, ...fields };
    });
  }
  const { register, handleSubmit } = useForm<FormData>({ resolver: zodResolver(schema) });

  const classNameInput =
    'block w-full flex-1 font-normal border-2 border-mako-100 focus:border-mako-500 rounded-md bg-transparent p-2 text-gray-900 placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6';

  const { currentStepIndex, next, step, steps, isLastStep } = useMultyStepForm([
    // <UserForm {...data} updateFields={updateFields} />,
    <div>
      <CustomLabelFields label=''>
        <input
          placeholder='test'
          {...register('firstName', {required: "this is a required"})}
          className={classNameInput}
        />
      </CustomLabelFields>
      <CustomLabelFields label=' '>
        <input
          placeholder='phone'
          {...register('phone', {required: "this is a required"})}
          className={classNameInput}
        />
      </CustomLabelFields>
    </div>,
    <UserFormOrder {...data} updateFields={updateFields} />,
    <div>3</div>,
  ]);

  const onSubmitData = (data: FormData) => {
    console.log('111', data)
  }

  const onSubmit = (e: FormEvent) => {
    e.preventDefault();
    handleSubmit(onSubmitData);
    next();

    currentStepIndex === steps.length - 2 && console.log(data);
  };

  return (
    <form onSubmit={onSubmit}>
      <Stepper activeStep={currentStepIndex}>
        {[...steps].map((el, index) => (
          <Step label='' key={index} />
        ))}
      </Stepper>

      <div className='mt-6 mb-3 sm:my-4 font-bold text-2xl sm:text-4xl text-center'>
         
      </div>

      {step}

      {!isLastStep && (
        <button
          type='submit'
          className='btn w-full rounded-3xl bg-curious-blue-500 text-white hover:bg-white-50 hover:text-curious-blue-500 hover:border-curious-blue-500 border-2 font-bold block p-4 mt-6 my-4 mx-1 grow'
        >
          
        </button>
      )}
    </form>
  );
}
