import React, { ChangeEvent } from 'react';
import CustomLabelFields from '../UI/custom-fields/CustomLabelFields';
import CustomInputFields from '../UI/custom-fields/CustomInputFields';
import { CustomInputPhone } from '../UI/custom-fields/CustomInputPhone';

type UserDataStepTwo = {
  firstName: string;
  lastName: string;
  phone: string;
};

type UserFormOrderProps = UserDataStepTwo & {
  updateFields: (fields: Partial<UserDataStepTwo>) => void;
};

export default function UserFormOrder({
  firstName,
  lastName,
  phone,
  updateFields,
}: UserFormOrderProps) {
  return (
    <>
      <CustomLabelFields label=''>
        <CustomInputFields
          placeholder=''
          readOnly
          required
          value={firstName}
          onChange={(e: ChangeEvent<HTMLInputElement>) =>
            updateFields({ firstName: e.target.value })
          }
        />
      </CustomLabelFields>
      <CustomLabelFields label=''>
        <CustomInputFields
          placeholder=''
          required
          autoFocus
          value={lastName}
          onChange={(e: ChangeEvent<HTMLInputElement>) =>
            updateFields({ lastName: e.target.value })
          }
        />
      </CustomLabelFields>
      <CustomLabelFields label=' '>
        <CustomInputFields
          type='tel'
          placeholder='+7(999) 888 77 66'
          required
          readOnly
          value={phone}
          onChange={(e: ChangeEvent<HTMLInputElement>) =>
            updateFields({ phone: e.target.value })
          }
        />
      </CustomLabelFields>
      {/* <CustomInputPhone label=" " /> */}
    </>
  );
}
